.App {
  text-align: center;
}
.watchedBox{
position: absolute;
float: right;
background-color: white;
top: 99px;
width: 100px;
height: 100vh;
right: 0px;
}
.watched{
  position: sticky;
  top: 0px;
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
  align-items: end;
  
  }
.watched img{
  border: 1px solid gray;
  border-radius: 10px;
  width: 100px;
  height: 100px;

}
.start{
  opacity: 0;
}
.end{
  opacity: 1;
  transition: all 1s;
}
.main-bg{
  height: 300px;
  background-image: url(./bg.png);
  background-size: cover;
  background-position: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
